import React, { useState, useEffect } from 'react';
import Axios from 'axios';

function Main() {
    const [search, setsearch] = useState('');
    const [data, setdata] = useState({ main: { temp: '' }, wind: '' })

    useEffect(() => {
        const fetchApi = async () => {
            const res = await Axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${search}&appid=f1213eed1fc7a1eb9dd4ab8823bf38a1&units=metric`);
            setdata(res.data);
        }
        fetchApi();
    }, [search]);
    return (
        <>
            <div className="main p-lg-3 p-1 text-center d-flex flex-column align-items-center">
                <div className="input-group">
                    <input className="form-control text-center" type="search" placeholder="enter name of city" value={search} onChange={(event) => { setsearch(event.target.value) }} />
                </div>

                <div className="mt-5">
                    <h2 className="location text-capitalize text-break">
                        <i class="fas fa-street-view mx-2"></i>{data.name}
                    </h2>
                </div>

                <div className="create-ani p-md-5 p-4">
                    <div className="text-data mx-auto">
                        <h3 className="mt-3 mb-0 fs-5">Feels like {data.main.temp}° cel</h3>
                        <p>Min: {data.main.temp_min}° cel | Max: {data.main.temp_max}° cel</p>
                        <p>Pressure: {data.main.pressure}</p>
                        <p>humidity: {data.main.pressure}</p>
                        <p>Wind Speed: {data.wind.speed}</p>
                    </div>
                </div>
            </div>
        </>
    );
}

export default Main;
